<template>
  <div>
    <el-menu
      style="
        width: 12vw;
        height: 92vh;
        position: fixed;
        background: rgba(255, 255, 255, 0.9);
        left: 0;
      "
      unique-opened
    >
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <div class="right">
      <h1 style="color: white; padding-top: 1vh">邮件回复模板管理</h1>
      <div style="color: white">
        点击上传邮件模板文件:
        <el-button type="warning" @click="goUpload()">上 传</el-button>
      </div>

      <table class="mailTemplate_table">
        <thead>
          <tr>
            <th>国家</th>
            <th>问题类型</th>
            <th>适用情况</th>
            <th>问题模板</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableData" :key="index">
            <td>{{ item.country }}</td>
            <td style="font-size:0.7rem;">{{ item.problem_type }}</td>
            <td>{{ item.Instructions_use }}</td>
            <td>{{ item.email_content }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 30vw"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      leftNavData: [], //左边导航数据
      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      tableData: [], //表格数据
      total: 0, //页码当前页
      page: 1 //
    };
  },
  methods: {
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/email_template_detail/",
        method: "get",
        params: {
          page: 1
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.data;
        this.total = res.data.count[0].count / 2;
      });
    },
    NavClick(index, index2, item3) {
      //点击左侧导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/email_template_detail/",
        method: "get",
        params: {
          platform: this.firstNav,
          country: this.twiceNav,
          language: this.threeNav,
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data;

        this.$nextTick(() => {
          $(".mailTemplate_table >tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 2;
        this.page = 1;
      });
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      axios({
        url: this._url2 + "server/customer_management/email_template_detail/",
        method: "get",
        params: {
          platform: this.firstNav,
          country: this.twiceNav,
          language: this.threeNav,
          page: this.page
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data;
        // tbody

        this.$nextTick(() => {
          $(".mailTemplate_table >tbody").scrollTop(0);
        });
      });
    },
    goUpload() {
      this.$router.push("/TemplateUploadManagement");
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data1;
    });
  }
};
</script>

<style scoped>
.right {
  margin-left: 12.7vw;
}
.right h1 {
  text-indent: 35vw;
}
.right a {
  display: inline-block;
  width: 5vw;
  height: 3vh;
  font-size: 0.9rem;
  background-color: skyblue;
  text-align: center;
  line-height: 3vh;
  color: white;
  border-radius: 5px;
  border: 1px solid #ededed;
}

table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 67vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 37vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 40px;
  line-height: 40px;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  /* height: 30vh; */
  line-height: 40px;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

table tr th:nth-child(1),
table tr td:nth-child(1) {
  width: 3vw;
}
table tr th:nth-child(2),
table tr td:nth-child(2) {
  width: 8vw;
}

table tr td:nth-child(1),
table tr td:nth-child(2) {
  font-size: 0.9rem;
}
</style>



